import "./BankAccountModal.css";
import { useState, useEffect } from "react";
import Modal from "./Modal";
import Toast from "../toast/Toast";
import copyToClipboard from "./CopyToClipboard";

function GroomBankAccountModal({ onClose }) {
  const [ToastStatus, setToastStatus] = useState(false);
  const [ToastMsg, setToastMsg] = useState(""); //   

  const handleToast = (text) => {
    if (!ToastStatus) {
      setToastStatus(true);
      setToastMsg(text);
    }
  };

  useEffect(() => {
    if (ToastStatus) {
      setTimeout(() => {
        setToastStatus(false);
        setToastMsg("");
      }, 1000);
    }
  }, [ToastStatus]);

  const banckAccount1 = "11112222333";
  const banckAccount2 = "55556667777";

  return (
    <Modal onClose={onClose}>
      <h3> </h3>
      <div className="bank_account_info">
         {banckAccount1} 
        <div
          className="bank_number_copy"
          onClick={() => {
            copyToClipboard(banckAccount1);
            handleToast(" ");
          }}
        >
          
        </div>
      </div>
      <div className="bank_account_info">
         {banckAccount2} 
        <div
          className="bank_number_copy"
          onClick={() => {
            copyToClipboard(banckAccount2);
            handleToast(" ");
          }}
        >
          
        </div>
      </div>
      {ToastStatus && (
        <>
          <Toast msg={ToastMsg} />
        </>
      )}
    </Modal>
  );
}

export default GroomBankAccountModal;
